.container {
    display: grid;
    grid-template-columns: 5rem 15rem 1fr 15rem;
    grid-template-rows: 100vh;
}

.serversList {
    background-color: #202225;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
}

.server {
    border-radius: 100%;
}

.server img{
    width: 100%;
    border-radius: 100%;
}


.channels {
    background-color: #2f3136;

}

.channelContent {
    background-color: #36393f;
}

.members {
    background-color: #2f3136;
}